.about-author {
  padding: 100px 70px 0;
}

.about-author__title {
  margin: 0;
  padding: 0 0 23px;
  font-weight: 400;
  font-size: 22px;
  line-height: calc(27 / 22);
  letter-spacing: -0.04em;
  border-bottom: 1px solid #000;
}

.about-author__container {
  margin: 66px 0 0;
  display: flex;
  flex-direction: row;
  column-gap: 10px;
  justify-content: space-between;
}

.about-author__author {
  max-width: 600px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.about-author__author-name {
  margin: 0 0 18px;
  font-style: normal;
  font-weight: 400;
  font-size: 50px;
  line-height: calc(58 / 50);
  letter-spacing: -0.04em;
}

.about-author__author-about {
  margin: 0 0 26px;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 20px;
}

.about-author__author-description {
  margin: 0 0 99px;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
}

.about-author__author-link {
  color: #000000;
  text-decoration: none;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  transition: opacity 0.3s linear;
}

.about-author__author-link:hover {
  opacity: 0.6;
}

.about-author__author-avatar {
  width: 270px;
  height: 327px;
  border-radius: 18px;
  object-fit: cover;
}
