.footer {
  flex: 0 0 auto;
  padding: 0 70px;
  margin: 79px 0 20px;
}

.footer__about {
  margin: 0;
  font-weight: 400;
  font-size: 13px;
  line-height: calc(16 / 13);
  text-align: center;
  color: #a0a0a0;
}

.footer__separator {
  margin: 20px auto;
  border-bottom: 1px solid #e8e8e8;
  display: block;
}

.footer__container {
  display: flex;
  justify-content: space-between;
}

.footer__year {
  margin: 0;
  font-weight: 400;
  font-size: 13px;
  line-height: calc(16 / 13);
}

.footer__nav-box {
  margin: 0;
  padding: 0;
  display: flex;
  list-style: none;
  column-gap: 20px;
}

.footer__nav-link {
  text-decoration: none;
  color: #000;
  font-weight: 400;
  font-size: 13px;
  line-height: calc(16 / 13);
}

@media screen and (max-width: 831px) {
  .footer {
    padding: 0 30px;
  }
}

@media screen and (max-width: 519px) {
  .footer {
    padding: 0 10px;
    margin-bottom: 10px;
  }
}

@media screen and (max-width: 425px) {
  .footer__about {
    font-size: 12px;
    line-height: calc(15 / 12);
  }
  .footer__separator {
    margin: 21px 0 30px;
    padding: 10px auto;
  }
  .footer__container {
    row-gap: 30px;
    flex-direction: column-reverse;
    align-items: center;
  }
  .footer__year {
    font-size: 12px;
    line-height: calc(15 / 12);
    color: #a0a0a0;
  }
  .footer__nav-box {
    row-gap: 12px;
    flex-direction: column;
    align-items: center;
  }
  .footer__nav-link {
    font-size: 12px;
    line-height: calc(15 / 12);
  }
}
